	module.exports = {"data":{"wordpressPost":{"title":"Editors’ Picks 2015: Part Four","content":"<h2><a href=\"http://bmjopen.bmj.com/content/5/2/e007519.full\"><strong>Long-term health effects of exercise for the elderly: study protocol</strong></a></h2>\n<p><a href=\"http://blogs.bmj.com/bmjopen/files/2016/01/Aerobics_P4.jpg\"><img class=\"alignnone size-medium wp-image-932\" src=\"http://blogs.bmj.com/bmjopen/files/2016/01/Aerobics_P4-300x200.jpg\" alt=\"Aerobics_P4\" width=\"300\" height=\"200\" srcset=\"http://blogs.bmj.com/bmjopen/files/2016/01/Aerobics_P4-300x200.jpg 300w, http://blogs.bmj.com/bmjopen/files/2016/01/Aerobics_P4-1024x683.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /></a></p>\n<p><strong>Each day this week we&#8217;ve been looking at a published paper from 2015 that sparked the interest of one of our staff editors. Our fourth pick is a study protocol for a randomised controlled trial of exercise training in an elderly population.</strong></p>\n<p><a href=\"http://bmjopen.bmj.com/site/about/guidelines.xhtml#studyprotocols\">BMJ Open supports the publication of study protocols</a> to improve transparency in the ‘research to publication’ process and to protect the wider community against numerous damaging research practices, such as <a href=\"http://www.bmj.com/content/347/bmj.f6170\">publication bias</a> and <a href=\"http://psr.sagepub.com/content/2/3/196.abstract\">HARKing</a>.</p>\n<p><a href=\"http://bmjopen.bmj.com/content/5/2/e007519.full\">In this study protocol</a>, <a href=\"https://www.ntnu.no/ansatte/dorthe.stensvold\">Dorthe Stensvold</a> and colleagues from Norway, Australia and the USA aim to evaluate the effects of 5 years of exercise training on mortality in an elderly population. Whilst previous epidemiological studies have indicated that physical activity is associated with reduced risk of premature death and that changes in fitness predict mortality, it is unclear whether the level of physical activity is the direct cause of favourable health effects rather than other factors associated with a person’s state of health.</p>\n<p>The proposed study in just under 1600 participants is considered to be the first randomised controlled trial to assess the effect of exercise training on mortality and morbidity in an elderly population. The protocol currently has <a href=\"https://scholar.google.co.uk/scholar?hl=en&amp;q=A+randomised+controlled+study+of+the+long-term+effects+of+exercise+training+on+mortality+in+elderly+people&amp;btnG=&amp;as_sdt=1%2C5&amp;as_sdtp=\">6 citations</a> in Google Scholar, suggesting that the article has been a useful addition to the scholarly literature since it was published in February 2015.</p>\n<p>If you are interested in learning more about study protocols then check out BMJ&#8217;s recently launched e-learning programme: <a href=\"http://www.bmj.com/company/who-we-are/case-studies/research-to-publication/\">Research to Publication</a>. The programme includes a free taster module on how to write and publish a study protocol. If you successfully complete the module, then you will receive formal recognition with a certificate from The BMJ’s Editor-in-Chief and UCSF’s associate dean. If you provide this certificate when you submit your study protocol to BMJ Open, then you will receive a 75% reduction on the open access publication fee if your article is accepted.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"1b39b744-c37c-5e10-bf0a-8b111e33d7dd"}}

